.button {
  font-family: 'Open Sans';
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  border: 2px solid #0A2640;
  border-radius: 24px;
  color: #0A2640;
  padding: 8px 40px;
  background-color: transparent;
  cursor: pointer; }
  .button:hover {
    content: "";
    transition: color ease 0.35s, transform ease 0.35s;
    transform: scale(1.1);
    color: #65E4A3; }
  .button.lg {
    padding: 16px 56px;
    font-size: 20px;
    line-height: 28px; }

* {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  min-width: 320px;
  background-color: #ffffff;
  font-size: 16px; }

li {
  list-style: none; }

a,
a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

.container {
  width: 100%;
  max-width: 1240px;
  padding: 0 20px;
  margin: 0 auto; }

.container {
  position: relative; }

.header {
  padding-top: 56px;
  position: fixed;
  width: 100%; }
  .header-inner {
    width: 1200px;
    display: flex;
    flex-wrap: wrap;
    align-items: center; }
  .header-logo {
    height: 42px; }
    .header-logo img {
      height: 100%;
      width: auto; }
  .header-nav {
    display: flex;
    max-width: 419px;
    margin-left: auto; }
  .header-button {
    display: flex; }

.menu {
  display: flex;
  margin-left: auto;
  width: 420px;
  justify-content: space-around; }
  .menu-item {
    display: flex;
    flex-wrap: wrap; }
  .menu a {
    color: #0A2640;
    font-family: 'Open Sans';
    font-weight: 600;
    font-size: 16px;
    line-height: 28px;
    transition: color ease 0.35s; }
    .menu a:hover {
      color: #65E4A3; }

.background-circle {
  display: inline-block;
  width: 1293px;
  height: 1293px;
  border-radius: 50%;
  background-color: #F1F1F1;
